@import url(https://fonts.googleapis.com/css?family=Lato);
* {
  margin: 0;
  padding: 0;
}

body {
    background-color: rgb(245, 245, 247);
    color: rgb(22, 22, 22);
    font-family: 'Lato', sans-serif;
    overflow-x: hidden;
}

.container {
    display: flex;
    flex-direction: row;
}
.lightsContainer {
    display: flex;
    flex-direction: column;
    max-width: 300px;
    width: 30%;
    height: calc(100vh - 100px);
    padding: 50px;
    overflow-y: scroll;
}

.lightsContainer input {
    padding: 20px;
    outline: none;
    border: none;
    border-radius: 5px;
    font-size: 15px;
    background-color: rgb(239, 239, 243);
}

.light {
    margin: 20px 0;
    background-color: white;
    border: 1px solid rgb(239, 239, 243);
    border-radius: 15px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 20px;
    cursor: pointer;
}

.light h3 {
    font-weight: bold;
    font-size: 22px;
}

.description {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin-top: 50px;
}

.description p {
    margin-bottom: 15px;
    color: rgb(136, 136, 136);
}

.color {
    width: 40px;
    height: 40px;
    border-radius: 5px;
}


.infoContainer {
    padding: 50px 0;
    background-color: white;
    width: 100%;
}

.grey {
    color: rgb(172, 172, 172);
}

.infoContainer p {
    font-size: bold;
}

.infoContainer h1 {
    margin-top: 20px;
}

.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 50px;
}

.header img {
    float: right;
    margin-top: 10px;
    cursor: pointer;
}

.date {
    font-size: 20px;
}

.infoCardsContainer {
    display: grid;
    grid-auto-flow: column;
    padding: 50px;
    grid-column-gap: 50px;
    grid-auto-columns: 1fr;
}

.brightnessCard {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.brightnessCardHeader {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    padding: 20px;
}

.brightnessCard p {
    font-size: 20px;
    font-weight: bold;
}

.brightnessFooter {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.brightnessFooter h1 {
    margin-bottom: 10px;
}

.rangeContainer {
    width: 80%;
    height: 50px;
    display: flex;
    align-items: center;
}

.input-range__label-container {
    display: none;
}

.effectCard {
    display: flex;
    justify-content: space-between;
    align-items: center;
    flex-direction: column;
}

.effectCard p {
    margin-top: 20px;
    font-size: 20px;
    font-weight: bold;
}

.buttonContainer {
    margin-bottom: 20px;
}

.buttonContainer > div {
    padding: 10px 20px;
    outline: none;
    border: none;
    border-radius: 5px;
    color: white;
    text-align: center;
    font-size: 15px;
    cursor: pointer;
    margin: 10px 0;
}

.switchContainer {
    width: 45px;
    height: 26px;
    border-radius: 14px;
    display: flex;
    align-items: center;
    padding: 0 3px;
}

.switchContainer div {
    width: 22px;
    height: 22px;
    background-color: white;
    border-radius: 22px;
}


.registerContainer {
    background-color: white;
    width: 100vw;
    height: 100vh;
    display: grid;
    grid-template-columns: 40% 60%;
}

.registerBanner {
    width: 100%;
    color: white;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    z-index: 20;
    overflow: hidden;
}


.registerBanner h1 {
    font-size: 3em;
    font-weight: bold;
}

.registerBanner p {
    width: 100%;
    max-width: 250px;
    text-align: center;
    color: rgba(255, 255, 255, 0.8);
    margin-top: 20px;
    line-height: 30px;
}

.signInButton {
    height: 50px;
    width: 250px;
    padding: 10px 30px;
    border: 2px solid white;
    border-radius: 25px;
    color: white;
    cursor: pointer;
    outline: none;
    margin-top: 20px;
    background-color: transparent;
    font-size: 15px;
    font-weight: 500;
}

.register {
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: white;
}

.register p {
    color: red;
}

.register h1 {
    font-size: 40px;
    text-align: center;
    margin-bottom: 30px;
    color: #66a6ff;
}

.registerForm {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.inputField {
    display: flex;
    flex-direction: column;
    margin: 10px 0;
}

.inputImg {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
}

.inputImg > div {
    height: 45px;
    width: 45px;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 5px 0 0 5px;
    background-color: rgb(239, 239, 243);
}

.inputField img {
    background-color: rgb(239, 239, 243);
}

.inputField input {
    padding: 10px;
    width: 90vw;
    max-width: 250px;
    height: 25px;
    border: none;
    outline: none;
    background-color: rgb(239, 239, 243);
    border-radius: 0 5px 5px 0;
}

.inputField span {
    color: red;
    font-size: 13px;
}

.registerForm button {
    height: 50px;
    width: 200px;
    padding: 10px 30px;
    border: none;
    border-radius: 25px;
    color: white;
    cursor: pointer;
    outline: none;
    margin-top: 20px;
    font-size: 15px;
}
.landingContainer {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    margin-top: 50px;
}

.landingContainer button {
    padding: 10px 20px;
    margin: 10px;
    border: none;
    outline: none;
    background-color: rgb(43, 124, 247);
    color: white;
    cursor: pointer;
}
